html {
    font-size: 20px;
}
html,body {
    width: 100%;
    min-height: 100%;
    overflow-x: hidden;
}

.g-wrapper {
    position: relative;
    padding-bottom: 7.5rem;
    width: 100%;
    background-color: #252432;
    line-height: 1;
}

* {
    box-sizing: border-box;
}

.g-header {
    margin-bottom: 1.75rem;
    width: 100%;
    
    img {
        margin-bottom: .25rem;
        width: 100%;
    }
    
    .u-button {
        margin: {
            left: auto;
            right: auto;
        };
        width: 9.55rem;
        height: 2.25rem;
        background: url(../img/button.png) 0 0 no-repeat;
        background-size: cover;
        font-size: .8rem;
        line-height: 2.25rem;
        color: #490518;
        text-align: center;
    }
}

.m-title {
    position: relative;
    left: 50%;
    margin-left: -1.9rem;
     margin-bottom: 1.5rem;
    width: 3.8rem;
    font-size: .7rem;
    color: #fff;
    text-align: center;
    
    &:before,
    &:after {
        content: "";
        position: absolute;
        top: 50%;
        margin-top: -.5px;
        width: 5.8rem;
        height: 1px;
        background-color: #fff;
    }
    
    &:before {
        left: -5.8rem;
    }
    &:after {
        right: -5.8rem;
    }
}

