.g-column-2 {
    padding: {
        left: 2rem;
        right: 1.6rem;
    };
    font-size: .7rem;
    line-height: 1.25rem;
    text-align: justify;
    color: #fff;
    
    p {
        position: relative;
        
        span {
            position: absolute;
            left: -1rem;
            width: 1rem;
        }
    }
    
    .m-title {
        margin-bottom: 1rem;
    }
}

.color-yellow {
    color: #ffda45;
}




.g-column-1 {
    .m-bonus-box {
        width: 13rem;
        margin: {
            bottom: 1.2rem;
            left: auto;
            right: auto;
        };
    }
    
    li {
        margin-bottom: .75rem;
        width: 100%;
        height: 3.5rem;
        background-color: #fff;
        border-radius: .3rem;
        
        .text1 {
            float: left;
            width: 4rem;
            height: 3.5rem;
            font-size: .75rem;
            line-height: 3.5rem;
            text-align: center;
            color: #090001;
            
            span {
                margin-left: 1.2rem;
                margin-top: .75rem;
                display: block;
                width: 2rem;
                height: 2rem;
                border: 2px solid #090001;
                font-size: 1.1rem;
                line-height: 2rem;
                background-color: #ffd946;
                border-radius: 100%;
            }
        }
        
        .text2 {
            padding-left: 4rem;
            width: 100%;
            font-size: .8rem;
            line-height: 3.5rem;
            color: #b1b1b1;
            text-align: left;
        }
    }
}